.CVBody {
	background-color: #ffffff;
	min-height: 200vh;

	
	justify-content: center;
	font-size: calc(10px + 2vmin);
	top: 300vh;
	z-index: -1;
	overflow: auto;

	padding:2rem;
	
}

.Profile-Paper {
	border-color: rgb(0, 136, 255);
	width: 60vw;
	height: 20vw;
	top: 4vh;
	left: 4vh;
	position: relative;
	border-radius: 5px !important;

}

.Profile-Avatar {
	
	border-radius: 20px !important;
	overflow: hidden;
}

.CVGrid{
	width: 100% !important;
}

.CVDesc{
	margin-top: 2rem;
	background-color: rgba(185, 210, 231, 0.075);
	font-style: italic;
	font-weight: lighter;
	font-size: 2rem !important;
	line-height: 4rem;
	border-radius: 10px !important;
	overflow: hidden;
}


/*
 * Mobile Version
 */
@media all and (max-width : 767px) {

	.Profile-Avatar {
		width:80% !important;
	}


	.CVBody {
		padding:1rem;
		
	}

}

/*
 * Desktop Version
 */
@media all and (min-width : 768px) {

	.Profile-Avatar {
		width:100% !important;
		height: 100% !important;
	}

	.CVBody {
		padding:2rem;
		
	}

}