.CVHeader {
	position: -webkit-sticky;
	position: sticky;
	top: 0;
	background: inherit;
	text-align: left;
	margin: 0 !important;
	z-index: 1;	
}

.CVHeader-Background {
	margin: auto;	
	position: absolute;

	bottom: 0;
	right:0;
	z-index: -1;
	-webkit-box-shadow: 0px 2px 15px 2px rgba(0,0,0,0.75);
	-moz-box-shadow: 0px 2px 15px 2px rgba(0,0,0,0.75);
	box-shadow: 0px 2px 15px 2px rgba(0,0,0,0.75);
	

}


.CVHeader-Logo-Img {
	margin: auto;	
	position: sticky;
	position: -webkit-sticky;
	top: 2vh;
	z-index: 1;

}

/*
 * Mobile Version
 */
@media all and (max-width : 767px) {
	.CVHeader {
		height: 18vw;
		max-height: 18vw;
		padding: 0vh !important;
	}
	.CVHeader-Logo-Img {
		margin: 0vh 0 0 4vw;
		height: 13vw;
	}
}

/*
 * Desktop Version
 */
@media all and (min-width : 768px) {
	.CVHeader {
		height: 10vh;
		max-height: 10vh;
		padding: 0 !important;
	}
	.CVHeader-Logo-Img {
		margin: 0vh 0 0 2vh;
		height: 6vh;
	}
}

